<template>
    <div>
       <div class="menu">
            <ul>
            <router-link to='/openHall'>
                <li>
                    <span class="winning_icon"></span>
                    <span>开奖</span>
                </li>
            </router-link>
            <router-link to='/choseHall'>
                <li>
                    <span class="buy_lottery_icon"></span>
                    <span>购彩</span>
                </li>
            </router-link>
            <router-link to='/recharge'>
                <li>
                    <span class="charge_icon"></span>
                    <span>充值</span>
                </li>
            </router-link >
            <router-link to='/person'>
                <li>
                    <span class="help_icon"></span>
                    <span>{{isLogin?'个人中心':'登录'}}</span>
                </li>
            </router-link>
            <div style="clear:both"></div>
            </ul>
       </div>
       <div class="most-popular">
           <div class="popular"><p class="popular-title">最受欢迎</p>
           <div class="roll_news">
              <ul :style={top} :class="{trans:activeIndex}">
                 <li v-for="item in news" >{{item}}</li>
              </ul>
           </div>
           </div>
           <ul>
              <router-link to="/dbc">
               <li>
                  <span class="dbc_icon"></span>
                  <i>双色球</i>
               </li>
               </router-link>
                <router-link to="/dlt">
               <li>
                  <span class="dlt_icon"></span>
                  <i>大乐透</i>
               </li>
               </router-link>
               <router-link to="/fc">
               <li>
                  <span class="fc_icon"></span>
                  <i>福彩3D</i>
               </li>
               </router-link>
               <router-link to="/plt">
               <li>
                  <span class="plt_icon"></span>
                  <i>排列3</i>
               </li>
               </router-link>
             <div style="clear:both"></div>
           </ul>
           <p class="domain-title"><span>大乐透</span>  <span style="color:#f00">奖池:{{jiangchi}}</span><span class="refresh"  @click="chose">换一注</span></p>
           <div class="letou-num">
               <p v-for="item in rotates" :class="item.rot==1?'rotate':''">{{item.count}}</p>
               <button>{{buy_one}}元购买</button>
               <div style="clear:both"></div>
           </div>
       </div>
       <ul class="populars">
          <p class="domain-title">热门彩种</p>
            <router-link to="/dbc">
           <li>
                  <span class="dbc_icon"></span>
                  <i>双色球</i>
               </li>
              </router-link>
              <router-link to="/dlt">
               <li>
                  <span class="dlt_icon"></span>
                  <i>大乐透</i>
               </li>
               </router-link>
               <router-link to="/fc">
               <li>
                  <span class="fc_icon"></span>
                  <i>福彩3D</i>
               </li>
               </router-link>
               <router-link to="/plt">
               <li>
                  <span class="plt_icon"></span>
                  <i>排列3</i>
               </li>
               </router-link>
               <router-link to="/plf">
               <li>
                  <span class="plf_icon"></span>
                  <i>排列五</i>
               </li>
               </router-link>
               <router-link to="/qxc">
               <li>
                  <span class="qxc_icon"></span>
                  <i>七星彩</i>
               </li>
               </router-link>
               <router-link to="/qlc">
               <li>
                  <span class="qlc_icon"></span>
                  <i>七乐彩</i>
               </li>
               </router-link>
             <div style="clear:both"></div>
       </ul>
    </div>
</template>
<script>
import axios from 'axios'
import config from '../../config/config'
export default {
    data(){
        return{
           rotates:[
               {count:'00',rot:1},
               {count:'00',rot:1},
               {count:'00',rot:1},
               {count:'00',rot:1},
               {count:'00',rot:1},
               {count:'00',rot:1},
               {count:'00',rot:1}
           ],
           isLogin:false,
           activeIndex:0,
           news:['[大乐透]166***4900喜中100元','[大乐透]166***4900喜中200元','[大乐透]166***4900喜中300元','[大乐透]166***4900喜中100元'],
           //将最后一条追加最后，无缝滚动
           timer:'',
           jiangchi:'7.99亿',
           buy_one:2
        }
    },
    created(){
       const that=this;
       this.chose();
       this.$nextTick(()=>{
           this.isLogin=this.$store.state.isLogin
       });
    },
    methods:{
        chose(){
            this.rotates.forEach((v,i)=>{
                v.rot=1;
                if(i<5){
                    v.count=config.randomJx(33);
                }else{
                    v.count=config.randomJx(12);
                }
                
                setTimeout(()=>{
                    v.rot=0;
                },500*(i+1))
            });
        }
    },
     computed: {
    top() {
      return - this.activeIndex * 50 + 'px';
    }
  },
  mounted() {
    window.timer=setInterval(()=> {
        console.log('-----');
      if(this.activeIndex+1<this.news.length){
         this.activeIndex+=1;
      }else{
          this.activeIndex=0;
      }
    }, 2000);
  },
  destroyed(){
      clearInterval(window.timer);
  },
}
</script>
<style  >
.most-popular{
    background:#fff;
    margin-bottom:.4rem;
}
.populars{
    background:#fff;
    width:100%;
}
.populars li span{width: 100%;
    height:50px;
    line-height:50px;
    display: block;
    background-repeat: no-repeat;
    background-position: center;
    background-size: 50px;}
.populars li i{
    font-style:normal;
    line-height:0.8rem;
    font-size:0.38rem;
}
.populars li{float:left;width:25%;text-align:center;}
.letou-num{
   height:1rem;
}
.letou-num p{
   height:0.7rem;
   width:0.7rem;
   line-height:0.8rem;
   border-radius:50%;
   background:#cc2b2f;
   display:inline-block;
   float:left;
   color:#fff;
   font-size:0.35rem;
   margin-left:0.2rem;
   text-align:center;
}
.letou-num p:nth-last-of-type(1){
    background: #2483F9;
}
.letou-num button{
    background:#cc2b2f;
    font-size:0.34rem;
    border:none;
    border-radius:3px;
    padding:0.1rem 0.2rem;
    float:right;
    color:#fff;
    margin-right:0.2rem;

}
.letou-num p:nth-last-of-type(2){
    background: #2483F9;
}
.most-popular ul{
    width:100%;
    border-bottom:1px solid #e2e2e2;
}
.most-popular li span{width: 100%;
    height:50px;
    line-height:50px;
    display: block;
    background-repeat: no-repeat;
    background-position: center;
    background-size: 50px;
}
.most-popular li i{
    font-style:normal;
    line-height:0.8rem;
    font-size:0.38rem;
}
.most-popular li{
    float:left;width:25%;
    text-align:center;
}
.popular-title{
    font-weight:900;
}
.domain-title{
    background:#fff;
    line-height:0.6rem;
    font-size:0.4rem;
    padding:0.2rem;
    font-weight:900;
}
.domain-title span{
    font-weight:normal;
}
.menu{
    width:100%;
    height:78px;
    margin-bottom:0.4rem;
    background:#fff;
    padding:0.4rem 0 0.2rem 0;
}
.menu ul{
    width:100%;
    height:78px;
    font-size:0.38rem;
    transition: .3s linear;
}
.menu li{
    width:25%;
    text-align:center;
    display:inline-block;
    float:left;
}
.menu li span{
    width: 100%;
    height:35px;
    line-height:35px;
    display: block;
    background-repeat: no-repeat;
    background-position: center;
    background-size: 30px;
}
.rotate {
    animation-name: num;
	animation-duration: 0.2s;
	animation-timing-function: linear;
	animation-iteration-count: infinite;
	-webkit-animation-name: num;
	-webkit-animation-duration: 0.2s;
	-webkit-animation-timing-function: linear;
	-webkit-animation-iteration-count: infinite;
	-moz-animation-name: num;
	-moz-animation-duration: 0.2s;
	-moz-animation-timing-function: linear;
	-moz-animation-iteration-count: infinite;
}
@-webkit-keyframes num {
0% {
-webkit-transform:rotate(0deg);
}
50% {
-webkit-transform:rotate(180deg)
}
100% {
-webkit-transform:rotate(360deg)
}
}
@-moz-keyframes num {
0% {
-moz-transform:rotate(0deg);
}
50% {
-moz-transform:rotate(180deg)
}
100% {
-moz-transform:rotate(360deg)
}
}
@keyframes num {
0% {
transform:rotate(0deg);
}
50% {
transform:rotate(180deg)
}
100% {
transform:rotate(360deg)
}
}
.popular{
    height:50px;
    padding:0 0.2rem;
}
.popular .popular-title{
    display:inline-block;
    width:40%;
    line-height:50px;
    font-size:0.4rem;
    color:#333;
}
.popular .roll_news{
    display:inline-block;
    float:right;
    overflow:hidden;
    height:50px;
    color:#666;
}
.roll_news li{
    height:50px;
    line-height:50px;
    width:100%;
    float:none;
}
.roll_news ul.trans{
 transition:0.5s;
}
.roll_news ul{ 
     position:relative;
}
.refresh{
    background-image:url('data:image/png;base64,');
    background-repeat:no-repeat;
    background-size:0.3rem;
    padding-left:0.3rem;
    background-position:left center;
    float:right;
}
</style>


